.App {
  padding: 0 100px;
}

.listTask {
  margin-bottom: 10px;
}

.task {
  float: left;
}

.action {
  float: right;
}

input{
  margin: 10px 0;
  border-radius: 6px;
  padding: 10px;
  width: 600px;
}

button{
  padding: 10px 20px;
  border-radius: 6px;
  color: white;
  background-color: #121212;
  margin-left: 10px;
}

.delete{
  background-color: #d70000;
  border: solid #d70000;
}

.todo_wrapper {
  list-style: none;
  padding: 0;
  display: grid;
}

.todo_item {
  margin-bottom: 10px;
}